.calendar-container{
    width: 100%;
    height: 40%;
    display:flex;
    justify-content:center;
    align-items:flex-start;
}

.wrapper{
    width: 90%;
    height: 100%;
    border-radius: 10px;
  }
  .header{
    border-bottom: 1px solid indigo;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    padding: 8px 0px;
  }
  .icons{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
  }
  .icons span{
    cursor: pointer;
    color: #878787;
    text-align: center;
    font-size: 25px;
    border-radius: 50%;
  }
  
  .icons span:hover{
    color: #453CC9;
  }
  .current-date{
    font-size: 25px;
    font-weight: bold;
    color: #453CC9;
    
  }
  
  .calendar ul{
    display: flex;
    margin-top: 0;
    padding: 0;
    justify-content: flex-start;
    flex-wrap: wrap;
    list-style: none;
    text-align: center;
    height: 0px;
  }
  .calendar li{
    color: #333;
    width: calc(100% / 7);
    font-size: 16px;
  }
  .calendar .weeks li{
    font-weight: bold;
    cursor: default;
    text-transform: uppercase;
  }

  .calendar .days li{
    z-index: 1;
    cursor: pointer;
    position: relative;
    margin-top: 8px;
  }
  .days li.inactive{
    color: #453CC9;
  }
  .days li.active{
    color: white;
    font-weight: bold;
  }
  .days li::before{
    position: absolute;
    content: "";
    left: 50%;
    top: 50%;
    padding: 12px 13px;
    z-index: -1;
    border-radius: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
  .days li.active::before{
    background: #453CC9;
    
  }
  .days li:not(.active):hover::before{
    background: #f2f2f2;
  }
