.cover{
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    width: 25%;
    height: 40vh;
    align-items: center;
    justify-content: space-around;
    position: relative;
    background-color: white;
    border-radius: 20px 20px 20px 20px;
    position: fixed;
    box-shadow: 0 6px 10px 4px rgba(0,0,0,0.15);
}



.placeholder{
    position: absolute;
    font-size: 13px;
    top: 10px;
    left: 8px;
    padding: 0px 5px;
    transition: 0.3s;
    pointer-events: none;


}
.input:focus + .placeholder,
.input:not(:placeholder-shown) 
+ .placeholder{
    top: -10px;
    color: #29ABF4;
    background-color: white;
    
}
.input-group{
    position: relative;
}
