div.tableau header{
    display: flex;
    align-self: stretch;
    grid-gap: 1em;
    gap: 1em;
    justify-content: flex-end;
}
div.tableau header input{
    border-radius: 10000px;
}
div.tableau header button:first-child{
    justify-self: flex-start;
}
div.tableau button{
    padding: .5em 1em;
    border-radius: 10000px;
    border: none;
}

div.tableau table{
    align-self: stretch;
    position: relative;
    border-collapse: collapse;
}
div.tableau thead{
    background: #D4E8FF;
    border-radius: 6px;
    overflow: hidden;
}
div.tableau th{
    padding: .5em 1em;
    text-align: left;
    position: -webkit-sticky;
    position: sticky;
}
div.tableau td{
    padding: .25em 1em;
}
div.tableau td button{
    width: 130px;
